Vue.component('module', {

    data() {
        return {
            name: 'Name',
            description: 'description',
            image: 'no'
        }
    },

    template: `
        <div class="col-md-12">
            <div class="row">
                <h3>
                    <img style="position: relative;margin-top: -10px" src="{{ image }}" height="60px">
                    {{ name }}
                </h3>
                
                <div class="row">
                    <div class="col-md-12">
                        <div class="bs-callout bs-callout-info" role="alert">
                            <span class="glyphicon glyphicon-info-sign" aria-hidden="true"></span>
                            {{ description }}
                        </div>
                    </div>
                </div>
                
                
                
            </div>
        </div>
        `
});

